  }
  .tz_portfolio_plus_image {
    z-index: auto;
  }
  #portfolio {
    margin-left: -20px;
    margin-right: -20px;
  }
  .TzLetters{
    padding: 5px 10px;
    border: 1px dashed @border-base;
    background: @background-gray;
    border-radius: 3px;
    .nav {
      margin: 0;
    }
    li{
      a{
        padding: 3px 7px;
      }
    }
  }

  #tz_options {
    .option-combo {
      margin-top: 10px;
      a.btn-default {
        border: 1px solid @border-base;
        box-shadow: none;
        background: none;
      }
      a.selected {
        background: @background-gray;
        box-shadow: 0 1px 0 rgba(255, 255, 255, 0.2) inset, 0 1px 2px rgba(0, 0, 0, 0.05);
        outline: none;
      }
      a:active{
        outline: none;
      }
    }
  }
  .element .TzInner {
    padding: 0;
    margin: 20px;
    line-height: inherit;
    border: none;
    position: relative;
    .TzArticleMedia {
      margin-bottom: 0;
      >div {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        position: relative;
        .ImageOverlayMg {
          background: none repeat scroll 0 0 rgba(0, 0, 0, 0.5);
          height: 100%;
          display: block;
          left: 0;
          opacity: 0;
          position: absolute;
          top: 0;
          -webkit-transition: all 0.3s ease 0s;
          -moz-transition: all 0.3s ease 0s;
          -ms-transition: all 0.3s ease 0s;
          -o-transition: all 0.3s ease 0s;
          transition: all 0.3s ease 0s;
          width: 100%;
        }
        .white-rounded {
          background-color: #ffffff;
          border: medium none;
          display: inline-block;
          float: none;
          font-size: 14px;
          font-weight: normal;
          height: 40px;
          line-height: 40px;
          margin: 0 2px;
          text-align: center;
          width: 40px;
          -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0.3), inset 0 0 2px rgba(0, 0, 0, .1);
          -moz-box-shadow: 0 0 1px rgba(0, 0, 0, 0.3), inset 0 0 2px rgba(0, 0, 0, .1);
          -ms-box-shadow: 0 0 1px rgba(0, 0, 0, 0.3), inset 0 0 2px rgba(0, 0, 0, .1);
          -o-box-shadow: 0 0 1px rgba(0, 0, 0, 0.3), inset 0 0 2px rgba(0, 0, 0, .1);
          box-shadow: 0 0 1px rgba(0, 0, 0, 0.3), inset 0 0 2px rgba(0, 0, 0, .1);
          >a {
            color: #222222;
            display: block;
            font-weight: normal;
            text-decoration: none;
          }
        }
        figure {
          display: none;
        }
        .iconhover {
          z-index: 3;
          span {
            position: absolute;
            &:nth-of-type(1) {
              bottom: 50%;
              top: 50%;
              left: 50%;
              margin: -20px 0 0 -82px;
              visibility: hidden;
              opacity: 0;
              z-index: 3;
              -webkit-transition: all 0.3s ease 0s;
              -moz-transition: all 0.3s ease 0s;
              -ms-transition: all 0.3s ease 0s;
              -o-transition: all 0.3s ease 0s;
              transition: all 0.3s ease 0s;
            }
            &:nth-of-type(2) {
              bottom: 50%;
              top: 50%;
              right: 50%;
              margin: -20px -82px 0 0;
              visibility: hidden;
              opacity: 0;
              background-color: lightsalmon;
              color: #ffffff;
              z-index: 3;
              -webkit-transition: all 0.3s ease 0s;
              -moz-transition: all 0.3s ease 0s;
              -ms-transition: all 0.3s ease 0s;
              -o-transition: all 0.3s ease 0s;
              transition: all 0.3s ease 0s;
              >a {
                color: #ffffff;
              }
            }
          }
        }
      }
    }
    .TzPortfolioDescription {
      padding: 80px 30px 30px;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
      background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%);
      background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#000000',GradientType=0 );
      z-index: 2;
      .TzPortfolioTitle {
        margin: 0;
        padding: 0;
        color: lightsalmon;
        a {
          color: lightsalmon;
        }
      }
      .muted {
        color: #ffffff;
        font-size: 90%;
        a {
          color: #ffffff;
        }
      }
      .tpMeta{
         margin: 5px 0 0;
         div {
           display: inline-block;
           line-height: 1;
           margin: 0 10px 0 0;
           vertical-align: middle;
         }
       }
      .charity {
        margin-top: 20px;
      }
      .TzPortfolioIntrotext {
        margin-top: 20px;
      }
      .TzPortfolioReadmore {
        border: 1px solid @border-base;
        box-shadow: none;
        color: darken(@border-base, 80%);
        background: none;
        margin-top: 15px;
      }
    }
    .tp-item-tools {
      margin: 0;
      padding: 12px 20px;
      background: rgba(0,0,0,0.9);
      line-height: 100%;
      position: absolute;
      color: #ffffff;
      top: 10px;
      z-index: 1;
      .tp-list-tools {
        list-style: none;
        margin: 0;
        padding: 0;
        li {
          display: inline;
          a {
            font-size: 80%;
            color: #ffffff;
          }
          margin-right: 15px;
          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
    &:hover {
      box-shadow: 0 0 5px rgba(0,0,0,0.06);
      border-bottom-color: #4396e6;
      .TzArticleMedia {
        >div {
          .iconhover {
            span {
              &:nth-of-type(1) {
                margin: -20px 0 0 -41px;
                visibility: visible;
                opacity: 1;
              }
              &:nth-of-type(2) {
                margin: -20px -41px 0 0;
                visibility: visible;
                opacity: 1;
              }
            }
          }
          .ImageOverlayMg {
            top: 0;
            opacity: 1;
          }
        }
      }
    }
  }
  #tz_append {
    margin: 15px 0;
    a {
      border-color: @border-base;
      text-transform: uppercase;
      font-size: 100%;
      font-weight: bold;
      padding: 10px 0;
      background: white;
    }
  }
}
